@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200&display=swap');
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}
.vertical{
  border-left: 4px solid red;
    height: 150px;
    margin-left: 8rem;
    margin-top: 2rem;
 
} 
.main{
  display: flex;
}
.footbar{
  width: 100%;
  background: #111;
  margin-top: 4rem;
}
.left{
  margin-left: 0%;
}
.middle{
  margin-left: 10%;
  color: aliceblue;
  
}
.right{
  margin-left: 20%;
}
.logos{
  margin-top: 3rem;
  
}
.logos>ul{
  list-style-type: none;
  
}
.logos>ul>span{
  margin-left: 80%;
  color: aliceblue;
}
.logos>ul>li>span{
    color: aliceblue;
}
.logos >ul >li{
    color: aliceblue;
}
.logo{
  color: red;
  margin-top: 4rem;
  margin-left: 5rem;
}
.logo > span{
    color: aliceblue;
}
.social{
  margin-left: 20px;
  font-size: smaller;
  margin-top: 1rem;
  color: red;
}
.talk{
  font-size: smaller;
  margin-top: 1rem;
  color: red;
  margin-left: -80px;
}
.bot{
  background-color: red;
  color: black;
  border-radius: 20%;
  font-weight: bolder;
  padding: 10px;
}
.text{
    color: aliceblue;
}
.bot:hover{
cursor: pointer;
}
.greet{
  color: aliceblue;

}
.topic>img{
   display: flex;
  height: 10rem;
  width: 10rem;
  margin-left: 5rem;
  margin-top: 2rem;
}