@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Mulish:wght@200&display=swap');
::-webkit-scrollbar{
    display: none;
}

.con{
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 80px;
    margin-top: 4rem;
    font-family: mulish;
}
.con > span{
    color: red;
}
.team{
    display: flex;
}
.title{
    margin-top: 5rem;
    margin-left: 41rem;
    border-bottom: 4px solid red;
    padding-left: 20px;
    width: 200px;
}
.title1{
    
    width: 34rem;
    margin-left: 31rem;
}
.person1{
    height: 35rem;
    width: 20%;
    background-color:transparent;
    margin-bottom: 3rem;
    margin-left: 150px;
    margin-top: 40px;
    border-radius: 15px; 
     box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
    cursor: pointer;
    transition: transform .1s;

}
.person1:hover{
    transform: scale(1.1);
}
.person1 > img{
    max-width: 40%;
    margin-top: 20px;
    margin-left: 85px;

}
.person1>h2{
    margin-left: 100px;
    font-family: poppins;
}
