@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap');
*{
    margin: 0;
}

.topbar{
    height: 4rem;
    width: 100%;
    display: flex;
    background-color: black;
    position: sticky;
    top: 0;
    z-index: 1;
}
.topbar__left > h1{
    display: flex;
    color: red;
    margin-left: 40px;
    padding-top: 10px;
    font-family: poppins;
}
.topbar__left > h1 > span{
    color: azure;
}
.info{
    margin-left: 60%;
    display: flex;
    float: right;
    padding: 25px;
    list-style-type: none;
    color: aliceblue;
}
.info a{
    float: left;
    display: flex;
    margin-left: 20px;
    padding: 0 20px;
    cursor: pointer;
    font-family: poppins;
    font-size: 13px;
} 
.info a:hover{
    background-color: red;
    border-radius: 5px;
    transition: cubic-bezier(0.165, 0.84, 0.44, 1);


}
.head.active{
    background-color: red;
    border-radius: 5px;
    transition: cubic-bezier(0.165, 0.84, 0.44, 1);

}
.topbar__left>img{
   display: flex;
   height: 4rem;
   margin-left: 3rem;

}


